.App {
  text-align: center;
  background-image: linear-gradient(to top, #9795f0 0%, #fbc8d4 100%);
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto";
}

.App h1 {
  margin: 0;
  padding: 10px;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
