.recipe {
  border-radius: 12%;
  box-shadow: 0px 5px 20px rgb(71, 71, 71);
  margin: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: white;
  align-items: center;
  width: 45%;
}

.recipe li {
  list-style: none;
  margin: 0rem 2rem;
}

.recipe img {
  margin: 1rem;
  width: 30%;
}
